              p-id="10276"
            ></path>
          </svg>
        </span>
      </div>
    </div>

    <div class="button">
      <input
        type="text"
        id="int"
        placeholder="添加任务"
        v-model="text"
        @keyup.enter="addText"
      />
      <svg
        t="1692513991294"
