</template>
<script>
export default {
  name: "ListS",
  data() {
    return {
      text: "",
      value1: "",
      value2: "",
      arrList: [],
      tmp: true,
      isChecked: false,
    };
  },
  created() {
    if (localStorage.getItem("list") != null) {
      this.arrList = JSON.parse(localStorage.getItem("list"));
    }
    console.log(this.arrList);
  },
  mounted() {
    this.$bus.$on("Data", this.dataBtn);
  },
  updated() {
    this.$bus.$emit("index", this.arrList.length);
    localStorage.setItem("list", JSON.stringify(this.arrList));
  },
  methods: {
    mover(index) {
      if (confirm("确定删除吗？")) {
        this.arrList.splice(index, 1);
      }
    },
    dataBtn(value) {
      this.tmp = value;
    },
    toggleCheckbox() {
      this.isChecked = !this.isChecked;
    },
    //方法
    addText() {
      let addList = {
        id: this.arrList.length,
        name: this.text,
        status: false,
        value: "",
      };
      this.arrList.push(addList);
      this.text = "";

      console.log(this.arrList);
    },
  },

  watch: {
    //监听
    op() {},
  },
  props: {
    msg: String,
  },
};
</script>